@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/* #1abc9c */
:root {
  --student_contact_theme: #2980b9;
  --student_contact_buttos: #3498db;
  --student_contact_circle_dark: #3498db;
  --student_contact_circle_light: #2980b9;
  --student_conatc_icon_light: #222f3e;
  --student_contact_icon_dark: #333;
  --student_contact_link: #2f3640;
}
.route_link {
  float: right;
  text-decoration: none;
  color: var(--contact_theme_link);
  font-weight: bold;
  font-size: 0.8rem;
}
.route_link:hover {
  text-decoration: none;
  color: #c0392b;
}
.container {
  position: relative;
  width: 100%;
  min-height: 90vh;
  padding: 2rem;
  /* background-color: #fafafa; */
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Poppins", sans-serif;
  background: url("../../../../assests/d3.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.form {
  width: 100%;
  max-width: 820px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.1);
  z-index: 15;
  overflow: hidden;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.contact_form {
  background-color: var(--student_contact_theme);
  position: relative;
}
.contact_us_main_form {
  padding: 2.3rem 2.2rem;
  z-index: 10;
  overflow: hidden;
  position: relative;
}
.circle {
  border-radius: 50%;
  background: linear-gradient(
    135deg,
    transparent 20%,
    var(--student_contact_buttos)
  );
  position: absolute;
}

.circle.one {
  width: 130px;
  height: 130px;
  top: 130px;
  right: -40px;
}

.circle.two {
  width: 80px;
  height: 80px;
  top: 10px;
  right: 30px;
}

.contact_form:before {
  content: "";
  position: absolute;
  width: 26px;
  height: 26px;
  background-color: var(--student_contact_theme);
  transform: rotate(45deg);
  top: 50px;
  left: -13px;
}

.title {
  color: #fff;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1;
  margin-bottom: 0.7rem;
}

.input_container {
  position: relative;
  margin: 1rem 0;
}

.input {
  width: 100%;
  outline: none;
  border: 3px solid #fafafa;
  background: none;
  padding: 0.6rem 1.2rem;
  color: #fff;
  font-weight: 500;
  font-size: 0.95rem;
  letter-spacing: 0.5px;
  border-radius: 15px;
  transition: 0.3s;
}

textarea.input {
  padding: 0.8rem 1.2rem;
  min-height: 150px;
  border-radius: 22px;
  resize: none;
  overflow-y: auto;
}

.input_container label {
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
  padding: 0 0.4rem;
  color: #fafafa;
  font-size: 0.9rem;
  font-weight: 400;
  pointer-events: none;
  z-index: 1000;
  transition: 0.5s;
}

.input_container.textarea label {
  top: 1rem;
  transform: translateY(0);
}

.btn {
  padding: 0.6rem 1.3rem;
  background-color: #fff;
  border: 3px solid #fafafa;
  font-size: 0.95rem;
  color: var(--student_contact_theme);
  line-height: 1;
  border-radius: 25px;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  margin: 0;
}

.btn:hover {
  background-color: transparent;
  color: #fff;
}

.input_container span {
  position: absolute;
  top: 0;
  left: 25px;
  transform: translateY(-50%);
  font-size: 0.8rem;
  padding: 0 0.4rem;
  color: transparent;
  pointer-events: none;
  z-index: 500;
}

.input_container span:before,
.input_container span:after {
  content: "";
  position: absolute;
  width: 10%;
  opacity: 0;
  transition: 0.3s;
  height: 5px;
  background-color: var(--student_contact_theme);
  top: 50%;
  transform: translateY(-50%);
}

.input_container span:before {
  left: 50%;
}

.input_container span:after {
  right: 50%;
}

.input_container.focus label {
  top: 0;
  transform: translateY(-50%);
  left: 25px;
  font-size: 0.8rem;
}

.input_container.focus span:before,
.input_container.focus span:after {
  width: 50%;
  opacity: 1;
}

.contact_info {
  padding: 2.3rem 2.2rem;
  position: relative;
}

.contact_info .title {
  color: var(--student_contact_theme);
}

.text {
  color: #333;
  margin: 1.5rem 0 2rem 0;
}
.validation_container {
  max-width: 380px;
  width: 100%;
  background-color: #bec5c7;
  border: 2px solid #2c3e50;
  margin: 10px 0;
  border-radius: 5px;
  position: relative;
  padding: 10px 25px;
  display: flex;
  flex-direction: column;
}
.validation_container .message {
  color: #2c3e50;
  font-size: 13px;
}
.information {
  display: flex;
  color: #555;
  margin: 0.7rem 0;
  align-items: center;
  font-size: 0.95rem;
}

.icon {
  width: 28px;
  margin-right: 0.7rem;
}

.social_media {
  padding: 2rem 0 0 0;
}

.social_media p {
  color: #333;
}

.social_icons {
  display: flex;
  margin-top: 0.5rem;
}

.social_icons a {
  width: 35px;
  height: 35px;
  border-radius: 5px;
  background: linear-gradient(
    45deg,
    var(--student_contact_icon_dark),
    var(--student_conatc_icon_light)
  );
  color: #fff;
  text-align: center;
  line-height: 35px;
  margin-right: 0.5rem;
  transition: 0.3s;
}

.social_icons a:hover {
  transform: scale(1.05);
}

.contact_info:before {
  content: "";
  position: absolute;
  width: 110px;
  height: 100px;
  border: 22px solid var(--student_contact_theme);
  border-radius: 50%;
  bottom: -77px;
  right: 50px;
  opacity: 0.3;
}

.big_circle {
  position: absolute;
  width: 500px;
  height: 500px;
  border-radius: 50%;
  background: linear-gradient(
    to bottom,
    var(--student_contact_circle_light),
    var(--student_contact_circle_dark)
  );
  bottom: 50%;
  right: 50%;
  transform: translate(-40%, 38%);
}

.big_circle:after {
  content: "";
  position: absolute;
  width: 360px;
  height: 360px;
  background-color: #fafafa;
  border-radius: 50%;
  top: calc(50% - 180px);
  left: calc(50% - 180px);
}
.route_link {
  color: var(--student_contact_link);
  float: right;
  font-weight: bold;
  font-size: 16px;
}
.square {
  position: absolute;
  height: 400px;
  top: 50%;
  left: 50%;
  transform: translate(181%, 11%);
  opacity: 0.2;
}

@media (max-width: 850px) {
  .contact_background {
    display: none;
  }
  .form {
    grid-template-columns: 1fr;
  }

  .contact_info:before {
    bottom: initial;
    top: -75px;
    right: 65px;
    transform: scale(0.95);
  }

  .contact_form:before {
    top: -13px;
    left: initial;
    right: 70px;
  }

  .square {
    transform: translate(140%, 43%);
    height: 350px;
  }

  .big_circle {
    bottom: 75%;
    transform: scale(0.9) translate(-40%, 30%);
    right: 50%;
  }

  .text {
    margin: 1rem 0 1.5rem 0;
  }

  .social_media {
    padding: 1.5rem 0 0 0;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 1.5rem;
  }

  .contact_info:before {
    display: none;
  }

  .square,
  .big_circle {
    display: none;
  }

  form,
  .contact_info {
    padding: 1.7rem 1.6rem;
  }

  .text,
  .information,
  .social_media p {
    font-size: 0.8rem;
  }

  .title {
    font-size: 1.15rem;
  }

  .social_icons a {
    width: 30px;
    height: 30px;
    line-height: 30px;
  }

  .icon {
    width: 23px;
  }

  .input {
    padding: 0.45rem 1.2rem;
  }

  .btn {
    padding: 0.45rem 1.2rem;
  }
}
